.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background-color: #EDE9DF;
  border: 1px solid black;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
}